.App {
  margin: 0;
  min-height: 100%;
  background-color: rgb(0, 0, 0);
  font-family: Arial,sans-serif;
  font-size: 14px;
  line-height: 20px;
  color: #333;
}

body {
  margin: 0;
  min-height: 100%;
  background-color: #fff;
  font-family: Arial,sans-serif;
  font-size: 14px;
  line-height: 20px;
  color: #333;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.land-img{
  width:100%;
  left:0;
}

* {margin: 0; padding: 0;}
#container {
  height: 100%; width:100%;
  background-color: #282c34;
}
#left, #middle {
  display: inline-block; 
  *display: inline; 
  zoom: 1; 
  vertical-align: top; 
  font-size: 12px;
}
.back-td{
  background-image: url('./Assets/land.png');
  background-repeat:no-repeat;
  background-size:100% 100%;
  background-position: center;
  border:none;
  width: 100%;
}
#left {
  width: 50%; 
}
#middle {
  width: 50%; 
  text-align:center;
  color:white;
  padding:20px;
}

.white-text{
  color: white;
}

.red-text{
  color:red
}

.green-text{
  color:hsla(120, 91%, 49%, 1)
}

.yellow-text {
  color:yellow
}


select{
  color: #000;
    font-family: Oliver !important;
    padding: 6px 20px;
    border: none;
    outline: 0;
    font-size: 2rem !important;
    border-radius: 5px;
    transition: all .5s ease-in-out;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    /* background: url(/images/site/arrow.svg) 75%/20% no-repeat #eee; */
}

.setting{
  display: flex;
  justify-content: center;
}

.cta{
  border: 0;
  background: #fff;
  border-radius: 5px;
  font-family: Oliver;
  transition: all .5s ease-in-out;
  padding: 15px 30px;
    font-size: 3rem;
    margin-left: 10px;
}

#mint .connect, #mint .cta, #mint .disconnect {
  padding: 15px 30px;
  font-size: 3rem;
}

.back-td{
  width:50%;
  /* display: block; */
}

.mintcount-info{
  color: blue;
  font-weight: 900;
}

